import React, { Component } from 'react'
import NavBar from '../navbar/NavBar'
import Footer from '../footer/Footer'
import './Play.css'
import axios from 'axios'
import Sound from 'react-sound'
import { Link } from 'react-router-dom'


class Play1 extends Component {
  constructor(props) {
    super(props);
    this.state = {
      audio: null,
    }
    this.getAudio = this.getAudio.bind(this);
  }
  
  getAudio() {
    axios.get(`${process.env.REACT_APP_API_URL}/my-audios?audio_two`)
    .then(resAudio =>{
      const theAudio = resAudio.data[0].audio_two
      this.setState({audio: theAudio});

    })
    .catch((err)=>{
      console.log(err)
    })
  }
  
  render() {
    return (
      <div>
        <div className="full-page-white">
          <NavBar />
          <h1 className="instruction">Escute, depois cante.<br/>Repita a melodia</h1>
          <Sound
            url={this.state.audio}
            playStatus={Sound.status.PLAYING}
          />
          <button onClick={this.getAudio}className="btn-play"><img className="play-button" src={'/img/Play Mobile.svg'} alt="play"/></button>
          <div className="next">
            <Link  to='/q1'><img className='next-image' src={'/img/Next@3x.png'} alt="next"/></Link>
          </div>
          <div className='legend-block-white'>
            <img className="legend-white" src={'/img/Legenda 1 Mobile.svg'} alt="progress"/>
          </div>
        </div>
        <Footer />
      </div>
    )
  }
}


export default Play1;