import React, {Component} from 'react'
import './NavBar.css'
import { Link } from 'react-router-dom'
import About from '../home/About'

class NavBar extends Component {
  constructor(props) {
    super(props)
    this.state = {
      active: false
    }
  }

  toogleActive = () => {
    this.setState({active: !this.state.active})
  }

  render () {
    if(this.state.active){
      return (
        <div>
          <About toogleActive={this.toogleActive}/>
          <div className='navbar'>  
            <div className="horizontal-menu">
              <a className="left-menu" onClick={this.toogleActive}>Sobre</a>
              <a className="right-menu" href='/about'>Compartilhe</a>
            </div>
            <button className="nav-button1"><Link to='/'><img src={'/img/Symbol Menu.png'} alt='Menu'/></ Link></button>
          </div>
       </div>
      )
    }
    
    return (
      <div className='navbar'>  
        <div className="horizontal-menu">
          <a className="left-menu" onClick={this.toogleActive}>Sobre</a>
          <a className="right-menu" href='/about'>Compartilhe</a>
        </div>
        <button className="nav-button1"><Link to='/'><img src={'/img/Symbol Menu.png'} alt='Menu'/></ Link></button>
      </div>

    )
  }
}

export default NavBar;